body {

  background-color: #f0f0f0; 
  background-image: url('./wallclock.jpg'); 
  background-repeat: no-repeat; 
  background-size: cover; 
}




.App {
  text-align: center;
  margin-top: 250px;
  font-family: fantasy;
}
.outlined-text {
  color: hsl(0, 2%, 9%); 
  text-shadow: 
      -1px -1px 0 #000,  
       1px -1px 0 #000,
      -1px  1px 0 #000,
       100px  100px 0 #000; 
}

h1 {
  font-size: 50px;
  color: aqua;
}

.Counter {
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 300px;
  margin: 0 auto;
}

h2 {
  font-size: 100px;
  margin-bottom: 60px;
  color: aqua;
}

button {
  background-color: #57af4c;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  margin-right: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

input {
  padding: 8px;
  margin-right: 10px;
  width: 70px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

input:focus {
  outline: none;
  border-color: #4caf50;
  box-shadow: 0 0 5px rgba(76, 175, 80, 0.5);
}